<template>
  <div>
    <h2>{{ song_info.name }}</h2>
    <h3>{{ song_info.composer }}</h3>
    <h4>Capo: {{ song_info.capo }}</h4>
    <p>{{ song_info.content }}</p>
  </div>
</template>

<script>
import { store } from "../store.js";

export default {
  name: "SongView",
  data() {
    return {
      store,
      song_info: {
        name: "ahoj",
        composer: "",
        capo: "",
        content: ""
      }
    };
  },
  created() {
    var collection;
    var isPublic = !this.$route.path.includes("user");
    if (isPublic) {
      collection = this.store.publicSongsCollection;
    } else collection = this.store.userSongsCollection;

    var that = this;
    collection
      .where("slug", "==", this.$route.params.song_slug)
      .get()
      .then(function(results) {
        var there = that;
        if (results.size == 1) {
          collection
            .doc(results.docs[0].id)
            .collection("history")
            .limit(1)
            .get()
            .then(function(latest_history) {
              there.song_info = latest_history.docs[0].data().song_info;
            });
        } else {
          console.log("duplicate song slugs");
        }
      });
  }
};
</script>

